"use client";

import { useState } from "react";
import Image from "next/image";
import {
  FaUsers,
  FaCut,
  FaCashRegister,
  FaUserShield,
  FaHistory,
  FaBars,
  FaUserEdit,
  FaSignOutAlt,
  FaTachometerAlt,
} from "react-icons/fa";

interface SidebarAdminProps {
  sidebarOpen: boolean;
  setSidebarOpen: (open: boolean) => void;
}

export default function SidebarAdmin({
  sidebarOpen,
  setSidebarOpen,
}: SidebarAdminProps) {
  return (
    <aside
      className={`${
        sidebarOpen ? "w-72" : "w-20"
      } bg-black shadow-xl transition-all duration-300 flex-shrink-0 border-r-2 border-[#FDFB03] flex flex-col`}
    >
      {/* Sidebar Header */}
      <div className="p-6 border-b-2 border-[#FDFB03]">
        <div className="flex items-center justify-between">
          <div
            className={`flex items-center space-x-3 ${
              !sidebarOpen && "justify-center"
            }`}
          >
            <Image
              src="/assets/logo.png"
              alt="Brocode Logo"
              width={40}
              height={40}
              className="object-contain"
            />
            {sidebarOpen && (
              <div>
                <h1 className="text-xl font-bold text-[#FDFB03]">Brocode</h1>
                <p className="text-sm text-gray-300">Admin Panel</p>
              </div>
            )}
          </div>
          <button
            onClick={() => setSidebarOpen(!sidebarOpen)}
            className="p-2 rounded-lg hover:bg-gray-800 transition-colors"
          >
            <FaBars className="text-[#FDFB03]" size={18} />
          </button>
        </div>
      </div>

      {/* Navigation Menu */}
      <nav className="flex-1 p-4 space-y-2">
        {/* Dashboard */}
        <div className="mb-4">
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg bg-[#FDFB03] text-black font-medium">
            <FaTachometerAlt size={18} />
            {sidebarOpen && <span>Dashboard</span>}
          </button>
        </div>
        {/* Manajemen Capster */}
        <div>
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
            <FaCut
              className="text-gray-300 group-hover:text-[#FDFB03]"
              size={18}
            />
            {sidebarOpen && (
              <span className="text-gray-300 group-hover:text-[#FDFB03]">
                Manajemen Capster
              </span>
            )}
          </button>
        </div>
        {/* Manajemen Kasir */}
        <div>
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
            <FaCashRegister
              className="text-gray-300 group-hover:text-[#FDFB03]"
              size={18}
            />
            {sidebarOpen && (
              <span className="text-gray-300 group-hover:text-[#FDFB03]">
                Manajemen Kasir
              </span>
            )}
          </button>
        </div>
        {/* Manajemen Layanan */}
        <div>
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
            <FaUsers
              className="text-gray-300 group-hover:text-[#FDFB03]"
              size={18}
            />
            {sidebarOpen && (
              <span className="text-gray-300 group-hover:text-[#FDFB03]">
                Manajemen Layanan
              </span>
            )}
          </button>
        </div>{" "}
        {/* Manajemen User */}
        <div>
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
            <FaUsers
              className="text-gray-300 group-hover:text-[#FDFB03]"
              size={18}
            />
            {sidebarOpen && (
              <span className="text-gray-300 group-hover:text-[#FDFB03]">
                Manajemen User
              </span>
            )}
          </button>
        </div>
        {/* Riwayat Reservasi */}
        <div>
          <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
            <FaHistory
              className="text-gray-300 group-hover:text-[#FDFB03]"
              size={18}
            />
            {sidebarOpen && (
              <span className="text-gray-300 group-hover:text-[#FDFB03]">
                Riwayat Reservasi
              </span>
            )}
          </button>
        </div>
      </nav>

      {/* Bottom Menu - Update Akun & Logout */}
      <div className="mt-auto p-4 border-t border-[#FDFB03] space-y-2">
        {/* Update Akun */}
        <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-gray-800 transition-colors group">
          <FaUserEdit
            className="text-gray-300 group-hover:text-[#FDFB03]"
            size={18}
          />
          {sidebarOpen && (
            <span className="text-gray-300 group-hover:text-[#FDFB03]">
              Update Akun
            </span>
          )}
        </button>

        {/* Logout */}
        <button className="w-full flex items-center space-x-3 p-3 rounded-lg hover:bg-red-900 transition-colors group">
          <FaSignOutAlt
            className="text-gray-300 group-hover:text-red-400"
            size={18}
          />
          {sidebarOpen && (
            <span className="text-gray-300 group-hover:text-red-400">
              Log Out
            </span>
          )}
        </button>
      </div>
    </aside>
  );
}
